.header {
  background: url("../assets/img/header.svg");
  height: 950px;
  width: 100%; }
  .header .header-container {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 90%;
    max-width: 1200px;
    margin: 90px auto 0; }
    .header .header-container__img {
      /* Style for "iPhone 7 S" */
      height: 750px;
      width: auto; }
    .header .header-container__text {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center; }
      .header .header-container__text--header {
        font-size: 4.5rem;
        font-weight: 700;
        letter-spacing: 2.92px;
        margin: 90px 0 0;
        padding: 0; }
      .header .header-container__text--paragraph {
        margin: 50px 0 0 0;
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 28px; }
      .header .header-container__text-button {
        margin: 100px 0 0;
        padding: 0;
        /* Style for "Rounded Re" */
        max-width: 300px;
        height: 50px;
        box-shadow: 0 10px 40px rgba(54, 54, 54, 0.15);
        border-radius: 25px;
        background-color: #ffffff;
        border: none; }
        .header .header-container__text-button--link {
          color: #363636;
          background-image: linear-gradient(to top, #eb402c 0%, #d90647 100%);
          background-clip: text;
          -webkit-background-clip: text;
          color: transparent;
          text-shadow: 0 10px 30px rgba(223, 27, 61, 0.3);
          font-size: 1.125rem;
          font-weight: 700;
          text-decoration: none;
          padding: 20px 50px; }

.highlight {
  font-weight: 700; }

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .navigation__logo {
    font-size: 3.125rem;
    padding: 40px 0 0 40px;
    margin: 0;
    font-weight: 700;
    margin: 0; }
  .navigation__list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 40px 40px 0 0;
    margin: 0; }
  .navigation__item {
    list-style-type: none; }
  .navigation__link {
    cursor: pointer;
    text-decoration: none;
    font-size: 1.125rem;
    text-transform: uppercase;
    line-height: 28px;
    color: #ffffff;
    margin: 0 0 0 60px;
    opacity: .8;
    transition: .4s all; }
    .navigation__link:hover {
      opacity: 1;
      text-decoration: line-through; }
    .navigation__link--active {
      text-decoration: line-through; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #ffffff;
  background: #ffffff;
  font-family: 'Lato', sans-serif; }
